      <div class="dropUp"  >
        <el-upload 
          action="http://120.26.95.9:8081/restler/uploadFile" 
          :accept="'.json'"
          :on-change="handleUpload" 
          :on-success="handleSubmitSuccess"
          :on-error="handleSubmitError"
          :auto-upload="false" 
          :file-list="fileList" 
          multiple 
          drag 
          ref="el" 
          :limit="1">
            <img src="../assets/images/云.svg">
            <div style="color:white;font-size: large;font-weight: bold;">上传文件</div><br/>
            <div class="upload__text">点击图标或将文件文件拖拽到此处上传</div>
            <div class="filestyle">仅限上传json格式文件</div>
        </el-upload>
      </div>
    </div>
  </div>

  <div style="height:50px;"></div>
  <!-- <el-button class="select-btn" @click="sendData(0)">Test</el-button>
  <el-button class="select-btn" @click="sendData(1)">FuzzLean</el-button>
