<script>
import axios from 'axios';
export default {
  data(){
        return {
            WAY:['Test','FuzzLean','Fuzz'],
            selectedWay:'',
        }
    },
  methods: {
    sendData(data) {
      var jsonString = '{"data" : "' + data + '"}';
      var obj = JSON.parse(jsonString);
      console.log(obj);
      // 发送数据到后端
      axios.post('http://120.26.95.9:8081/restler/sendData', obj, {
            headers: {
              'Content-Type': 'application/json', // 设置请求头中的 Content-Type
              'Access-Control-Allow-Origin': '*', // 允许所有来源的跨域请求
              'Access-Control-Allow-Methods': 'POST', // 允许 POST 方法的跨域请求
            }
          })
        .then(response => {
          console.log(response.data);
        })
        .catch(error => {
          console.error(error);
        });
    },
    // 处理文件添加
    handleUpload(file) {
      console.log(file);
      if(file.status!="ready")
        return;
      const isJson = file.raw.type === 'application/json';
      if (isJson) {
        console.log(file.raw.name + "上传成功");
        this.fileList = [];
        this.fileList.push(file);
        this.$message({
          message: '上传成功',
          type: 'success'
        });

        setTimeout(() => {
          this.$message.closeAll();
        }, 2000);
      } else {
        this.fileList = [];
        this.$message.error('仅限上传json格式文件');
      }
    },
    // 真正向后端提交
    postFiles() {
      console.log(this.$refs);
      if (this.fileList.length === 0) {
        alert('请上传文件');
        return;
      }
      this.$refs.el.submit();
    },
    // 处理提交结果
    handleSubmitSuccess(){
      this.$message({
        message: '提交成功',
        type: 'success'
      });
      this.$message.info('即将进行分析');
    },
    handleSubmitError(){
      this.$message({
        message: '服务器繁忙，请稍后再试~',
        type: 'error'
      });
    },


    downloadFile() {
      // 下载文件
      // 假设使用axios发送请求
      console.log(1);
      axios.get('http://120.26.95.9/restler/downloadFile', {
        responseType: 'blob'  // 指定响应类型为二进制数据
      }).then(response => {
          
          const url = window.URL.createObjectURL(new Blob([response.data]));
          const link = document.createElement('a');
          link.href = url;
          link.setAttribute('download', 'file.txt');
          document.body.appendChild(link);
          link.click();
        })
        .catch(error => {
          console.error(error);
        });
    }
  }
}
</script>
